.header{min-width:100%;display:flex;justify-content:space-between;align-items:center;gap:4px;position:relative;z-index:10}.header__mobile{display:flex;gap:4px}.header__logo-wrapper{display:flex;gap:12px}.header__logo-wrapper a{display:flex;justify-content:center;align-items:center;gap:6px}.header__logo-wrapper a img{display:none}@media(min-width: 601px){.header__logo-wrapper a img{display:block;width:var(--spacing-6);height:var(--spacing-6)}}.header__text{color:#fff;font-family:"Pokemon-Solid",sans-serif;cursor:pointer}.header__search{background:#fff;display:flex;align-items:center;justify-content:center;gap:8px;border:1px solid #dedede;border-radius:6px;padding:4px 12px}@media(min-width: 601px){.header__search{padding:4px 12px}}.header__search-icon{width:12px;height:12px;cursor:pointer;color:#cbcbcb;transition:color 1s ease-in-out,transform 1s ease-in-out;display:none}.header__search-icon:hover{color:#e54222;transition:color 1.1s ease-in-out,transform .7s ease-in-out;transform:scale(1.1)}.header__icon-toggle{display:block}.header__search-input{width:var(--spacing-5);border:none;outline:none;flex:1;color:#e54222;transition:width .3s ease}.header__search-input::placeholder{color:#cbcbcb;font:var(--font-preset-label-3)}.header__input-toggle{width:100px;opacity:1}